import MainSlider from "./modules/main-slider";
import VideoPlayer from "./modules/playVideo";
import MiniSlider from "./modules/mini-slider";
import showBlocks from "./modules/showBlocks";
import Form from "./modules/form";
import ShowInfo from "./modules/showInfo";
import Download from "./modules/download";

window.addEventListener("DOMContentLoaded", function () {
  const mainSlider = new MainSlider({ container: ".page", btns: ".next" });
  mainSlider.render();

  new VideoPlayer(".showup__video .play", ".overlay").init();
  new VideoPlayer(".module__video-item .play", ".overlay").init();
  

  const showUpSlider = new MiniSlider({
    container: ".showup__content-slider",
    prev: ".showup__prev",
    next: ".showup__next ",
    activeClass: "card-active",
    animate: true,
  });
  showUpSlider.init();

  const modulesSlider = new MiniSlider({
    container: ".modules__content-slider",
    prev: ".modules__info-btns .slick-prev",
    next: ".modules__info-btns .slick-next",
    activeClass: "card-active",
    animate: true,
    autoplay: true
  });
  modulesSlider.init();

  const feedSlider = new MiniSlider({
    container: ".feed__slider",
    prev: ".feed__slider .slick-prev",
    next: ".feed__slider .slick-next",
    activeClass: "feed__item-active",
  });
  feedSlider.init();

  new showBlocks('.officerold', '.officernew', '.officer__card-item').init()
  new Form('.form').init()

  const moduleSlider = new MainSlider({container: '.moduleapp', btns: '.next', prev: '.prevmodule', next: '.nextmodule'})
        moduleSlider.render();

  new ShowInfo('.plus__content').init()
  new Download('.download').init()
});
